.projects__container {
    display: grid;
    grid-template-columns: 3fr 2fr;
    gap: 2.5rem;
    padding-bottom: 7rem;
}

.projects__container2 {
    display: grid;
    grid-template-columns: 2fr 3fr;
    gap: 2.5rem;
    padding-bottom: 7rem;
}

.projects__container3{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 2.5rem;
    padding-bottom: 7rem;
}


.projects__text{
    margin-top: 2rem;
}

.projects__container > div:hover {
    cursor: default;
}

.projects__content {
    background: var(--color-bg-variant);
}
.left{
    text-align: left;
}

.btn-projects {
    width: max-content;
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    font-weight: 600;
    background: var(--color-primary);
    color: var(--color-bg);
    padding: 0.6rem 1.1rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);

}

.btn-projects:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.btn-projects-secondary {
    width: max-content;
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    font-weight: 600;
    background: transparent;
    color: var(--color-primary);
    padding: 0.6rem 1.1rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}

.btn-projects-secondary:hover {
    background: var(--color-white);
    color: var(--color-bg);
    border-color: transparent;
}

.projects__right > h2 {
    color: var(--color-primary);
    font-weight: 600;
}
.projects__left > h2{
    color: var(--color-primary);
    font-weight: 600;
}

.github-button{
    font-size: 1.3rem;
    margin-right: 0.3rem;
}

.youtube-button{
    font-size: 1.3rem;
    margin-right: 0.3rem;
    line-height: 10rem;
}

.projetcs__carousel{
    transition: var(--transition);
    --animn: pulse;
}

.projetcs__carousel:hover{
    border-radius: 0rem 0.5rem 0.5rem 0.5rem;

    border: 8px solid var(--color-bg-variant);
}

.projects__morebuttons{
    display: flex;
    flex-direction: row;
    gap: 1rem;
}

.projects__smallerProjects{
    background: var(--color-bg-variant);
    padding: 2rem;
    border-radius: 1rem;
    border: 2px solid transparent;
    transition: var(--transition);
}


.projects__smallerProjects:hover{
    background: transparent;
    border: 2px solid var(--color-primary);
    transition: var(--transition);
}

/* MEDIA QUERIES (MEDIUM DEVICES) */
@media screen and (max-width:1024px) {
    .projects__container {
        grid-template-columns: 1fr;
    }
    .projects__container2 {
        grid-template-columns: 1fr;
    }

    .projects__container > div {
        width: 80%;
        margin: 0 auto;
    }

    .projects__container2 > div {
        width: 80%;
        margin: 0 auto;
    }

    .projects__container3{
        grid-template-columns: 1fr 1fr;
    }
    

}

/* MEDIA QUERIES (SMALL DEVICES) */
@media screen and (max-width:600px) {
    .projects__container {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .projects__container > div{
        width: 100%;
        padding: 2rem 1rem;
    }

    .projects__container2 {
        grid-template-columns: 1fr;
        gap: 1rem;
    }

    .projects__container2 > div{
        width: 100%;
        padding: 2rem 1rem;
    }

    .projects__container3{
        grid-template-columns: 1fr;
    }



}